/**
 * Kit of Parts page route (/kit-of-parts) - Next.js App Router
 * 
 * This page contains the Kit of Parts functionality - a 3D room builder.
 * It's a client component since it uses React hooks, Three.js, and browser APIs.
 */
'use client';

import React, { useCallback, useRef, useState } from "react";
import { Canvas, useThree } from "@react-three/fiber";
import { OrbitControls, Grid } from "@react-three/drei";
import { useStore, createStore } from "usestore-react";
import Link from "next/link";
import { useEffect } from "react";
import ErrorBoundary from './ErrorBoundary';
import './kit-parts.scss';
import Floor from "../../components/new/ground.jsx";
import B1 from "../../components/new/b1";
import Bb from "../../components/new/bb";
import FP from "../../components/new/FP";
import K1 from "../../components/new/K1";
import K2 from "../../components/new/K2";
import S1 from "../../components/new/S1";
import ST1 from "../../components/new/ST1";
import * as THREE from "three";

const selectedObjectIdStore = createStore("selectedObjectId", null);
const bathroomStore = createStore("kitOfParts_clickbathroom2", false);
const boxbedStore = createStore("kitOfParts_boxbedState", false);
const frontStore = createStore("kitOfParts_frontState", false);
const kitchenStore = createStore("kitOfParts_kitchenState", false);
const k2Store = createStore("kitOfParts_k2State", false);
const s1Store = createStore("kitOfParts_s1State", false);
const st1Store = createStore("kitOfParts_st1State", false);
const cameraStore = createStore("kitOfParts_cameraState", false);
const objectsStore = createStore("kitOfParts_objectsState", false);
const sceneRotationStore = createStore("kitOfParts_sceneRotation", true);

export default function KitOfPartsPage() {
  const [objects, setObjects] = useStore("kitOfParts_objectsState", false);
  const [bathroom, setbathroom] = useStore("kitOfParts_clickbathroom2", false);
  const [boxbed, setboxbed] = useStore("kitOfParts_boxbedState", false);
  const [front, setfront] = useStore("kitOfParts_frontState", false);
  const [kitchen, setkitchen] = useStore("kitOfParts_kitchenState", false);
  const [k2, setK2] = useStore("kitOfParts_k2State", false);
  const [selectedObjectId] = useStore("selectedObjectId", null);
  const [s1, setS1] = useStore("kitOfParts_s1State", false);
  const [st1, setSt1] = useStore("kitOfParts_st1State", false);

  useEffect(() => {
    console.log('KitOfPartsPage mounted');
  }, []);

  useEffect(() => {
    console.log('Store values updated:', { bathroom, boxbed, front, kitchen, k2, s1, st1 });
  }, [bathroom, boxbed, front, kitchen, k2, s1, st1]);
  const objectsList = [
    "bathroom",
    "boxbed",
    "front porch",
    "kitchen",
    "k2",
    "s1",
    "st1",
  ];
  const [InfoIsOpen, setOpen] = useState(false);
  // Removed modal state - no welcome popup

  // Keep handleOpen function for potential future use
  function handleOpen() {
    // Function kept for later use
  }
  function handleInfo() {
    setOpen(!InfoIsOpen);
  }
  function handleObjectsOnCanvas(objectType) {
    if (objectType === "bathroom") {
      setbathroom(!bathroom);
    }
    if (objectType === "boxbed") {
      setboxbed(!boxbed);
      console.log("boxbed", boxbed);
    }
    if (objectType === "front") {
      setfront(!front);
    }
    if (objectType === "kitchen") {
      setkitchen(!kitchen);
    }
    if (objectType === "k2") {
      setK2(!k2);
    }
    if (objectType === "s1") {
      setS1(!s1);
    }
    if (objectType === "st1") {
      setSt1(!st1);
    }
  }
  const [cameraState, setCameraState] = useState("cameraState");
  const ref = useRef(null);

  const onButtonClick = useCallback(async () => {
    if (ref.current === null) {
      return;
    }

    try {
      const { toPng } = await import("html-to-image");
      const dataUrl = await toPng(ref.current, { cacheBust: true });
      const link = document.createElement("a");
      link.download = "download.png";
      link.href = dataUrl;
      link.click();
    } catch (err) {
      console.log(err);
    }
  }, [ref]);

  const [sceneRotation, setSceneRotation] = useStore("kitOfParts_sceneRotation", true);
  const [isZoom, setZoom] = useState("");
  const [disableRotation, setDisableRotation] = useState(false);
  const [buttonTriggered, setbuttonTriggered] = useState(false);
  const [draggingMode, setDraggingMode] = useState(false);
  const [guiMode, setGuiMode] = useState(false);


  const handleButton = () => {
    setbuttonTriggered(!buttonTriggered);
  };
  useEffect(() => {
    if (buttonTriggered) {
      setDisableRotation(false);
    } else {
      setDisableRotation(true);
    }
  }, [buttonTriggered]);

const axoModeToggle = () => {
    if(draggingMode){
        setDraggingMode(!draggingMode);
    }
    setGuiMode(!guiMode);
    setDisableRotation(false);
}

const dragModeToggle = () => {
    if(guiMode){
        setGuiMode(!guiMode);
    }
    setDraggingMode(!draggingMode);
    setDisableRotation(false);
}

const planModeToggle = () => {
    setDraggingMode(false);
    setGuiMode(false);
    setDisableRotation(!disableRotation);
}

const toggleRotation = () => {
    setSceneRotation(!sceneRotation);
}

  const CameraDolly = ({ isZoom, draggingMode, guiMode }) => {
    const state = useThree();

    useEffect(() => {
      if (draggingMode === true) {
        state.camera.position.set(0, 27, 0);
        state.camera.zoom = 17
      }
      if (guiMode === true) {
        state.camera.position.set(-5.16, 19.9, 8.7);
        state.camera.zoom = 17
      }
      if (disableRotation === true || sceneRotation === true) {
        state.camera.zoom = 17
      }
    }, [draggingMode, guiMode, disableRotation, sceneRotation, state.camera]);

    return null;
  };

  const DeselectHandler = ({ children, draggingMode }) => {
    const [, setSelectedObjectId] = useStore("selectedObjectId", null);
    
    return (
      <group
        onClick={(e) => {
          if (draggingMode && e.target === e.currentTarget) {
            setSelectedObjectId(null);
          }
        }}
      >
        {children}
      </group>
    );
  };

  return (
      <div ref={ref} style={{ width: '100vw', height: '100vh', position: 'relative', zIndex: 1 }}>
        {/* Welcome modal removed */}

        {/* Navigation Section moved to layout.js - near corners */}
        {/* Object List Section moved to InventoryPanel component */}

        {/* Main Content Section */}
        <main className="kit-parts-main">
          <ErrorBoundary>
            <Canvas
              dpr={[1, 2]} 
              orthographic 
              camera={{ position: [-14, 11, 15], zoom: 13 }}
              style={{
                background: "#1C1C1C",
                height: "100vh",
                width: "100vw",
                position: "relative",
                zIndex: 1,
                pointerEvents: "auto",
              }}
            >
              <ambientLight intensity={0.6} />
              <directionalLight 
                position={[10, 10, 5]} 
                intensity={0.5}
              />
              <directionalLight 
                position={[-10, 5, -5]} 
                intensity={0.3}
              />

              <DeselectHandler draggingMode={draggingMode}>
                <group rotation={[0, 0, 0]}>
                  <Grid
                    position={[0, 1.01, 0]}
                    followCamera={false}
                    args={[32, 26]}
                    cellColor={"#6f6f6f"}
                    cellSize={5}
                    cellThickness={0.2}
                    sectionColor={"#9d4b4b"}
                    sectionSize={1}
                  />
                  <B1 show={bathroom} isZoomState={isZoom} isMouseDragging={guiMode} isAxio={draggingMode} freeView={sceneRotation}/>
                  <Bb show={boxbed} isZoomState={isZoom} isMouseDragging={guiMode} isAxio={draggingMode} freeView={sceneRotation}/>
                  <FP show={front} isZoomState={isZoom} isMouseDragging={guiMode} isAxio={draggingMode} freeView={sceneRotation}/>
                  <K1 show={kitchen} isZoomState={isZoom} isMouseDragging={guiMode} isAxio={draggingMode} freeView={sceneRotation}/>
                  <K2 show={k2} isZoomState={isZoom} isMouseDragging={guiMode} isAxio={draggingMode} freeView={sceneRotation}/>
                  <S1 show={s1} isZoomState={isZoom} isMouseDragging={guiMode} isAxio={draggingMode} freeView={sceneRotation}/>
                  <ST1 show={st1} isZoomState={isZoom} isMouseDragging={guiMode} isAxio={draggingMode} freeView={sceneRotation}/>
                  <Floor />
                </group>
              </DeselectHandler>
              <OrbitControls 
                autoRotate={sceneRotation} 
                enableRotate={disableRotation && !(draggingMode && selectedObjectId)}
                enablePan={!(draggingMode && selectedObjectId)}
                enableZoom={!(draggingMode && selectedObjectId)}
                autoRotateSpeed={0.5}
              />
              <CameraDolly isZoom={isZoom} draggingMode={draggingMode} guiMode={guiMode} />
            </Canvas>
          </ErrorBoundary>
        </main>


        {/* Control panel removed from frontend - functions kept for later use */}
        {/* axoModeToggle, dragModeToggle, planModeToggle, onButtonClick functions are preserved above */}

        <style jsx>
          {`
            /* Navigation styles moved to globals.scss */

            /* Main Content Section */
            .kit-parts-main {
              position: relative;
              width: 100vw;
              height: 100vh;
              z-index: 1;
              pointer-events: auto;
            }



          `}
        </style>

      </div>
  );
}
